/* General styles */
.header {
  position: fixed;
  background-color: #333;
  color: #fff;
  padding: 1rem;
  width: 100%;
  top: 0;
  left: 0;
  height: 60px;
  z-index: 50;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo img {
  width: 100px;
  height: auto;
}

.menuButton {
  display: none;
  cursor: pointer;
  position: relative;
  width: 30px;
  height: 20px;
}

.menuButton .bar {
  background-color: #fff;
  height: 3px;
  width: 100%;
  position: absolute;
  transition: all 0.3s ease;
}

.menuButton .bar:nth-child(1) {
  top: 0;
}

.menuButton .bar:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

.menuButton .bar:nth-child(3) {
  bottom: 0;
}

.menuButton.open .bar:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.menuButton.open .bar:nth-child(2) {
  opacity: 0;
}

.menuButton.open .bar:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -5px);
}

/* Menu styles */
.menu {
  display: flex;
}

.menu ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu li {
  margin: 0 1rem;
}

.menu a {
  color: #fff;
  text-decoration: none;
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  position: relative;
  padding-bottom: 0.25rem; /* Space for underline */
  transition: all 0.3s ease;
}

/* Hover effect */
.menu a:hover::after {
  width: 100%;
}

/* Underline effect on click */
.menu a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  background-color: #fff;
  transition: width 0.3s ease;
}

/* Active underline */
.menu a.active::after {
  width: 100%;
}

/* Mobile styles */
@media (max-width: 768px) {
  .menu {
    display: none;
    position: absolute;
    top: 80px;
    left: 0;
    width: 100%;
    background-color: #333;
    flex-direction: column;
    z-index: 100;
    transition: transform 0.3s ease, opacity 0.3s ease;
    transform: translateY(-100%);
    opacity: 0;
  }

  .menu.open {
    display: flex;
    transform: translateY(0);
    opacity: 1;
  }

  .menu ul {
    flex-direction: column;
    margin: 0;
    padding: 0;
  }

  .menu li {
    padding: 0.5rem 0;
    text-align: center;
    opacity: 0;
    transform: translateY(-20px);
    transition: opacity 0.3s ease, transform 0.3s ease;
  }

  .menu.open li {
    opacity: 1;
    transform: translateY(0);
  }

  .menuButton {
    display: block;
  }
}
